      this.$store.commit('getLingCard', 'color')
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.isLingWin = true
      vuesList.lingCardType = 'color'
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
    },
    goToStep104 () {
      this.step = 104
      this.$store.commit('changeHeader', true)
      this.$store.commit('changeNotDotHeadOne', true)
      this.$store.commit('changeNoteDot', true)
      this.$store.commit('changeNotDotHead', true)
      this.$store.commit('changeFrameDot', true)
      this.$store.commit('lingWin', true)
      this.$store.commit('getLingCard', 'color')
      // 塞入 local storage
      let vuesList = JSON.parse(localStorage.getItem('vuexList'))
      vuesList.isLingWin = true
      vuesList.lingCardType = 'color'
      localStorage.setItem('vuexList', JSON.stringify(vuesList))
      //
    },
    goToStep114 () {
      this.step = 114
    },
    goToStep105 () {
      this.step = 105
    },
    goToStep106 () {
      this.step = 106
    },
    goToStep107 () {
      this.step = 107
    }
  }
}
</script>
<style lang="scss" scoped>

  .haunted-house {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;

    .house-0 {

      &-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-image: url('~@/assets/images/ling/ling-color-house.png');
      }

      &-ghost {
        width: 100%;
        height: auto;
        position: absolute;
        top: 0px;
      }

      &-talk-box {
        display: flex;
        flex-direction: column;
      }

      &-head {
        padding: 4px 16px;
        margin: 150px auto 0px;
        text-align: center;
        background: #95ff00;
        color: black;
        border-radius: 30px;
        opacity: 0.7;
                z-index: 1;
      }

      &-frame {
        margin-top: 10px;
        max-height: 200px;
        padding: 20px;
        color: white;
        overflow: scroll;
        color: black;
        background: #95ff00;
        z-index: 1;
        opacity: 0.7;
      }

      &-triangle-box {
        margin-top: 16px;
      }

      &-triangle {
        margin: auto;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 15px 15px 0 15px;
        border-color: black transparent transparent transparent;
      }

      // zero step false

      &-all-box {
        position: absolute;
        width: 100vw;
        height: 100vh;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
        background-color: rgba(0, 0, 0, 0.8);
        padding-top: 25vh;
        z-index: 1;
      }

      &-all-btn {
        width: 100%;
        color: white;
        text-align: center;
        background: gray;
        margin-top: 30px;
        padding: 20px 10px;
      }
    }

    .house-3 {

      &-box {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        background-position-y: center;
      }

      &-text {
        color: white;
        padding: 10px;
        margin-top: 100px;
      }

      &-question {
        color: white;
        margin-top: 20px;
      }

      &-input {
        width: 150px;
        padding: 4px;
        color: black;
        margin-top: 10px;
      }

      &-btn {
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background: #95ff00;
        color: black;
        margin-top: 10px;
        border-radius: 100%;
      }
    }
  }

</style>



